"use client";
import { useRouter as useNextRouter, usePathname } from "next/navigation";
import { useCallback, useEffect } from "react";
import { useGlobalLoader } from "./useGlobalLoader";

export const useRouterWithLoader = () => {
  const router = useNextRouter();
  const pathname = usePathname();
  const { showMiniLoader, hideMiniLoader } = useGlobalLoader();

  // í˜ì´ì§€ ì´ë™ ì™„ë£Œ ì‹œ ë¡œë” ìˆ¨ê¸°ê¸°
  useEffect(() => {
    // pathnameì´ ë³€ê²½ë˜ë©´ ë¡œë”ë¥¼ ìˆ¨ê¹€
    hideMiniLoader();
  }, [pathname, hideMiniLoader]);

  // ê²½ë¡œ ë¹„êµ ë° ì™¸ë¶€ ë„ë©”ì¸ ì²´í¬ í•¨ìˆ˜
  const shouldShowLoader = useCallback((href: string): boolean => {
    try {
      let targetPath: string;
      let isExternalDomain = false;

      // ì ˆëŒ€ URLì¸ ê²½ìš°
      if (href.startsWith('http')) {
        const targetUrl = new URL(href);
        const currentDomain = window.location.hostname;
        
        // ì™¸ë¶€ ë„ë©”ì¸ì¸ì§€ í™•ì¸
        isExternalDomain = targetUrl.hostname !== currentDomain;
        targetPath = targetUrl.pathname;
      } 
      // ìƒëŒ€ URLì¸ ê²½ìš°
      else {
        targetPath = href.split('?')[0].split('#')[0]; // ì¿¼ë¦¬íŒŒë¼ë¯¸í„°ì™€ í•´ì‹œ ì œê±°
      }
      
      // ì™¸ë¶€ ë„ë©”ì¸ì´ê±°ë‚˜ í˜„ì¬ ê²½ë¡œì™€ ê°™ì€ ê²½ìš° ë¡œë” í‘œì‹œí•˜ì§€ ì•ŠìŒ
      if (isExternalDomain || targetPath === pathname) {
        return false;
      }
      
      return true;
    } catch {
      // URL íŒŒì‹± ì‹¤íŒ¨ ì‹œ - ì™¸ë¶€ ë§í¬ê°€ ì•„ë‹Œ ê²½ìš°ì—ë§Œ ë¡œë” í‘œì‹œ
      return !href.startsWith('http');
    }
  }, [pathname]);

  // router.pushë¥¼ ë˜í•‘í•˜ì—¬ í•„ìš”í•œ ê²½ìš°ì—ë§Œ ë¡œë”ë¥¼ í‘œì‹œ
  const push = useCallback(
    (href: string, options?: { scroll?: boolean }) => {
      if (shouldShowLoader(href)) {
        showMiniLoader("í˜ì´ì§€ë¥¼ ì´ë™í•˜ëŠ” ì¤‘ì…ë‹ˆë‹¤.");
      }
      return router.push(href, options);
    },
    [router, showMiniLoader, shouldShowLoader]
  );

  // router.replaceë¥¼ ë˜í•‘í•˜ì—¬ í•„ìš”í•œ ê²½ìš°ì—ë§Œ ë¡œë”ë¥¼ í‘œì‹œ
  const replace = useCallback(
    (href: string, options?: { scroll?: boolean }) => {
      if (shouldShowLoader(href)) {
        showMiniLoader("í˜ì´ì§€ë¥¼ ì´ë™í•˜ëŠ” ì¤‘ì…ë‹ˆë‹¤.");
      }
      return router.replace(href, options);
    },
    [router, showMiniLoader, shouldShowLoader]
  );

  // ë‹¤ë¥¸ router ë©”ì„œë“œë“¤ì€ ê·¸ëŒ€ë¡œ ì „ë‹¬
  return {
    ...router,
    push,
    replace,
  };
};
