/**
 * snake_case ë¬¸ìì—´ì„ camelCaseë¡œ ë³€í™˜
 * @param str snake_case ë¬¸ìì—´
 * @returns camelCase ë¬¸ìì—´
 */
export const toCamelCase = (str: string): string => {
  return str.replace(/_([a-z])/g, (_, letter) => letter.toUpperCase());
};

/**
 * camelCase ë¬¸ìì—´ì„ snake_caseë¡œ ë³€í™˜
 * @param str camelCase ë¬¸ìì—´
 * @returns snake_case ë¬¸ìì—´
 */
export const toSnakeCase = (str: string): string => {
  return str.replace(/[A-Z]/g, letter => `_${letter.toLowerCase()}`);
};

/**
 * ê°ì²´ì˜ í‚¤ë¥¼ snake_caseì—ì„œ camelCaseë¡œ ë³€í™˜
 * @param obj snake_case í‚¤ë¥¼ ê°€ì§„ ê°ì²´
 * @returns camelCase í‚¤ë¥¼ ê°€ì§„ ê°ì²´
 */
export const keysToCamelCase = <T extends Record<string, any>>(
  obj: T
): { [K in keyof T as K extends string ? CamelCase<K> : K]: T[K] } => {
  if (obj === null || obj === undefined) {
    return obj as any;
  }

  if (Array.isArray(obj)) {
    return obj.map(item => 
      typeof item === 'object' && item !== null ? keysToCamelCase(item) : item
    ) as any;
  }

  if (typeof obj === 'object') {
    const result: Record<string, any> = {};
    
    for (const [key, value] of Object.entries(obj)) {
      const camelKey = toCamelCase(key);
      
      if (typeof value === 'object' && value !== null && !Array.isArray(value)) {
        result[camelKey] = keysToCamelCase(value);
      } else if (Array.isArray(value)) {
        result[camelKey] = value.map(item =>
          typeof item === 'object' && item !== null ? keysToCamelCase(item) : item
        );
      } else {
        result[camelKey] = value;
      }
    }
    
    return result as any;
  }

  return obj as any;
};

/**
 * ê°ì²´ì˜ í‚¤ë¥¼ camelCaseì—ì„œ snake_caseë¡œ ë³€í™˜
 * @param obj camelCase í‚¤ë¥¼ ê°€ì§„ ê°ì²´
 * @returns snake_case í‚¤ë¥¼ ê°€ì§„ ê°ì²´
 */
export const keysToSnakeCase = <T extends Record<string, any>>(
  obj: T
): { [K in keyof T as K extends string ? SnakeCase<K> : K]: T[K] } => {
  if (obj === null || obj === undefined) {
    return obj as any;
  }

  if (Array.isArray(obj)) {
    return obj.map(item => 
      typeof item === 'object' && item !== null ? keysToSnakeCase(item) : item
    ) as any;
  }

  if (typeof obj === 'object') {
    const result: Record<string, any> = {};
    
    for (const [key, value] of Object.entries(obj)) {
      const snakeKey = toSnakeCase(key);
      
      if (typeof value === 'object' && value !== null && !Array.isArray(value)) {
        result[snakeKey] = keysToSnakeCase(value);
      } else if (Array.isArray(value)) {
        result[snakeKey] = value.map(item =>
          typeof item === 'object' && item !== null ? keysToSnakeCase(item) : item
        );
      } else {
        result[snakeKey] = value;
      }
    }
    
    return result as any;
  }

  return obj as any;
};

// íƒ€ì… í—¬í¼ë“¤
type CamelCase<S extends string> = S extends `${infer P1}_${infer P2}${infer P3}`
  ? `${P1}${Uppercase<P2>}${CamelCase<P3>}`
  : S;

type SnakeCase<S extends string> = S extends `${infer C}${infer T}`
  ? C extends Uppercase<C>
    ? `_${Lowercase<C>}${SnakeCase<T>}`
    : `${C}${SnakeCase<T>}`
  : S; 