// Select Component for @semicolon/community-core

import React, { forwardRef, useEffect, useRef, useState, useMemo } from 'react';

export interface SelectOption<T = any> {
  id: string | number;
  label: string;
  value: T;
  icon?: string;
  description?: string;
  disabled?: boolean;
}

export interface SelectProps<T = any> {
  /** ì˜µì…˜ ëª©ë¡ */
  options: SelectOption<T>[];
  /** ì„ íƒëœ ê°’ */
  value?: T;
  /** ê°’ ë³€ê²½ ì‹œ í˜¸ì¶œë˜ëŠ” í•¨ìˆ˜ */
  onChange: (option: SelectOption<T>) => void;
  /** í”Œë ˆì´ìŠ¤í™€ë” í…ìŠ¤íŠ¸ */
  placeholder?: string;
  /** ë¹„í™œì„±í™” ì—¬ë¶€ */
  disabled?: boolean;
  /** ë¼ë²¨ */
  label?: string;
  /** ì—ëŸ¬ ë©”ì‹œì§€ */
  error?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ */
  helper?: string;
  /** í¬ê¸° variant */
  size?: 'sm' | 'md' | 'lg';
  /** ì „ì²´ ë„ˆë¹„ ì‚¬ìš© ì—¬ë¶€ */
  fullWidth?: boolean;
  /** ë‹¤ì¤‘ ì„ íƒ ì§€ì› ì—¬ë¶€ */
  multiple?: boolean;
  /** ê²€ìƒ‰ ê¸°ëŠ¥ í™œì„±í™” ì—¬ë¶€ */
  searchable?: boolean;
  /** ê²€ìƒ‰ í”Œë ˆì´ìŠ¤í™€ë” */
  searchPlaceholder?: string;
  /** ì•„ì´ì½˜ í‘œì‹œ ì—¬ë¶€ */
  showIcon?: boolean;
  /** ì„¤ëª… í‘œì‹œ ì—¬ë¶€ */
  showDescription?: boolean;
  /** ë“œë¡­ë‹¤ìš´ ìµœëŒ€ ë†’ì´ */
  maxHeight?: string;
  /** ë¹ˆ ê²°ê³¼ ë©”ì‹œì§€ */
  noOptionsText?: string;
  /** ì»¨í…Œì´ë„ˆ í´ë˜ìŠ¤ ì´ë¦„ */
  className?: string;
  /** ë“œë¡­ë‹¤ìš´ í´ë˜ìŠ¤ ì´ë¦„ */
  dropdownClassName?: string;
  /** ë¼ë²¨ í´ë˜ìŠ¤ ì´ë¦„ */
  labelClassName?: string;
  /** ì—ëŸ¬ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  errorClassName?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  helperClassName?: string;
}

const Select = forwardRef<HTMLDivElement, SelectProps>(({
  options,
  value,
  onChange,
  placeholder = 'ì„ íƒí•´ì£¼ì„¸ìš”',
  disabled = false,
  label,
  error,
  helper,
  size = 'md',
  fullWidth = true,
  multiple = false,
  searchable = false,
  searchPlaceholder = 'ê²€ìƒ‰...',
  showIcon = false,
  showDescription = false,
  maxHeight = '240px',
  noOptionsText = 'ì˜µì…˜ì´ ì—†ìŠµë‹ˆë‹¤',
  className = '',
  dropdownClassName = '',
  labelClassName = '',
  errorClassName = '',
  helperClassName = '',
}, ref) => {
  const [isOpen, setIsOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  
  const dropdownRef = useRef<HTMLDivElement>(null);
  const selectId = `select-${Math.random().toString(36).substr(2, 9)}`;

  // ì„ íƒëœ ì˜µì…˜ ì°¾ê¸°
  const selectedOption = useMemo(() => {
    if (multiple) return null;
    return options.find(option => option.value === value);
  }, [options, value, multiple]);

  // í¬ê¸°ë³„ í´ë˜ìŠ¤ ì •ì˜
  const sizeClasses = {
    sm: 'h-8 px-2 text-sm',
    md: 'h-10 px-3 text-sm',
    lg: 'h-12 px-4 text-base'
  };

  // ë“œë¡­ë‹¤ìš´ ì™¸ë¶€ í´ë¦­ ê°ì§€
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target as Node)
      ) {
        setIsOpen(false);
        setSearchTerm('');
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const handleOptionClick = (option: SelectOption) => {
    if (option.disabled) return;
    onChange(option);
    if (!multiple) {
      setIsOpen(false);
      setSearchTerm('');
    }
  };

  const toggleDropdown = () => {
    if (!disabled) {
      setIsOpen(!isOpen);
      setSearchTerm('');
    }
  };

  // ê¸°ë³¸ select í´ë˜ìŠ¤
  const baseSelectClasses = `
    w-full
    font-sans
    bg-white
    border
    rounded-lg
    transition-all
    duration-200
    focus:outline-none
    focus:ring-2
    focus:ring-blue-500/20
    disabled:opacity-50
    disabled:cursor-not-allowed
    disabled:bg-gray-50
    flex
    items-center
    justify-between
    cursor-pointer
  `;

  // ìƒíƒœë³„ í…Œë‘ë¦¬ ìƒ‰ìƒ
  const borderClasses = error
    ? 'border-red-500 focus:border-red-500'
    : 'border-gray-300 focus:border-blue-500 hover:border-gray-400';

  // select í´ë˜ìŠ¤ ì¡°í•©
  const selectClasses = `
    ${baseSelectClasses}
    ${sizeClasses[size]}
    ${borderClasses}
    ${className}
  `.trim().replace(/\s+/g, ' ');

  return (
    <div
      ref={ref}
      className={`${fullWidth ? 'w-full' : 'w-auto'} relative`}
    >
      {/* ë¼ë²¨ */}
      {label && (
        <label
          htmlFor={selectId}
          className={`
            block
            text-sm
            font-medium
            text-gray-700
            mb-2
            font-sans
            ${disabled ? 'opacity-50' : ''}
            ${labelClassName}
          `.trim().replace(/\s+/g, ' ')}
        >
          {label}
        </label>
      )}

      {/* Select ë²„íŠ¼ */}
      <div
        ref={dropdownRef}
        id={selectId}
        role="button"
        tabIndex={disabled ? -1 : 0}
        aria-expanded={isOpen}
        aria-haspopup="listbox"
        onClick={toggleDropdown}
        className={selectClasses}
      >
        <div className="flex items-center gap-2 flex-1 overflow-hidden">
          {selectedOption ? (
            <span className="truncate text-gray-900">{selectedOption.label}</span>
          ) : (
            <span className="text-gray-500 truncate">{placeholder}</span>
          )}
        </div>

        {/* í™”ì‚´í‘œ ì•„ì´ì½˜ */}
        <svg
          className={`w-4 h-4 text-gray-400 transition-transform duration-200 flex-shrink-0 ${
            isOpen ? 'rotate-180' : ''
          }`}
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M19 9l-7 7-7-7"
          />
        </svg>
      </div>

      {/* ë“œë¡­ë‹¤ìš´ */}
      {isOpen && (
        <div
          className={`absolute top-full left-0 right-0 mt-1 bg-white border border-gray-300 rounded-lg shadow-lg z-50 overflow-hidden ${dropdownClassName}`}
        >
          {/* ì˜µì…˜ ëª©ë¡ */}
          <div
            className="overflow-y-auto"
            style={{ maxHeight }}
            role="listbox"
          >
            {options.length === 0 ? (
              <div className="px-3 py-8 text-center text-gray-500 text-sm">
                {noOptionsText}
              </div>
            ) : (
              options.map(option => {
                const isSelected = option.value === value;

                return (
                  <button
                    key={option.id}
                    type="button"
                    role="option"
                    aria-selected={isSelected}
                    onClick={() => handleOptionClick(option)}
                    disabled={option.disabled}
                    className={`
                      w-full px-3 py-2 text-left flex items-center gap-2 text-sm cursor-pointer
                      transition-colors duration-150
                      ${option.disabled
                        ? 'opacity-50 cursor-not-allowed bg-gray-50'
                        : isSelected
                          ? 'bg-blue-50 text-blue-600'
                          : 'hover:bg-gray-100 text-gray-900'
                      }
                      border-b border-gray-100 last:border-b-0
                    `.trim().replace(/\s+/g, ' ')}
                  >
                    <div className="flex-1 min-w-0">
                      <div className="truncate">{option.label}</div>
                      {showDescription && option.description && (
                        <div className="text-xs text-gray-500 mt-1 truncate">
                          {option.description}
                        </div>
                      )}
                    </div>
                  </button>
                );
              })
            )}
          </div>
        </div>
      )}

      {/* ì—ëŸ¬ ë©”ì‹œì§€ */}
      {error && (
        <p className={`
          mt-2
          text-sm
          text-red-500
          font-sans
          ${errorClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {error}
        </p>
      )}

      {/* ë„ì›€ë§ í…ìŠ¤íŠ¸ */}
      {helper && !error && (
        <p className={`
          mt-2
          text-sm
          text-gray-500
          font-sans
          ${helperClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {helper}
        </p>
      )}
    </div>
  );
});

Select.displayName = 'Select';

export { Select };