import {
  hideAllLoaders,
  hideLoading,
  hideMiniLoading,
  selectUIState,
  setLoadingText,
  setMiniLoadingText,
  showLoading,
  showMiniLoading
} from "@redux/Features/UI/uiSlice";
import { useCallback } from "react";
import { useAppDispatch, useAppSelector } from "./index";

export const useGlobalLoader = () => {
  const dispatch = useAppDispatch();
  const { isAutoTransitioned } = useAppSelector(selectUIState);

  // ê¸°ë³¸ ë¡œë” (ë¯¸ë‹ˆë¡œë” ì‚¬ìš©) - ê²Œì‹œê¸€ ì‘ì„± ì™¸ ëª¨ë“  ìƒí™©ì—ì„œ ì‚¬ìš©
  const showLoader = useCallback((text?: string) => {
    dispatch(showMiniLoading(text));
  }, [dispatch]);

  const hideLoader = useCallback(() => {
    dispatch(hideMiniLoading());
  }, [dispatch]);

  const setLoaderText = useCallback((text?: string) => {
    dispatch(setMiniLoadingText(text));
  }, [dispatch]);

  const withLoader = useCallback(
    async <T>(asyncFn: () => Promise<T>): Promise<T> => {
      try {
        showLoader();
        const result = await asyncFn();
        return result;
      } finally {
        hideLoader();
      }
    },
    [showLoader, hideLoader]
  );

  // ê²Œì‹œê¸€ ì‘ì„±ìš© í’€ìŠ¤í¬ë¦° ë¡œë”
  const showPostLoader = useCallback((text?: string) => {
    dispatch(showLoading(text));
  }, [dispatch]);

  const hidePostLoader = useCallback(() => {
    // ìë™ ì „í™˜ëœ ê²½ìš° ëª¨ë“  ë¡œë”ë¥¼ ì¢…ë£Œ
    if (isAutoTransitioned) {
      dispatch(hideAllLoaders());
    } else {
      dispatch(hideLoading());
    }
  }, [dispatch, isAutoTransitioned]);

  const setPostLoaderText = useCallback((text?: string) => {
    dispatch(setLoadingText(text));
  }, [dispatch]);

  const withPostLoader = useCallback(
    async <T>(asyncFn: () => Promise<T>): Promise<T> => {
      try {
        showPostLoader();
        const result = await asyncFn();
        return result;
      } finally {
        hidePostLoader();
      }
    },
    [showPostLoader, hidePostLoader]
  );

  // Mini ë¡œë” ê´€ë ¨ í•¨ìˆ˜ë“¤ (ì§ì ‘ ì‚¬ìš©í•  ë•Œ)
  const showMiniLoader = useCallback((text?: string) => {
    dispatch(showMiniLoading(text));
  }, [dispatch]);

  const hideMiniLoader = useCallback(() => {
    dispatch(hideMiniLoading());
  }, [dispatch]);

  const setMiniLoaderText = useCallback((text?: string) => {
    dispatch(setMiniLoadingText(text));
  }, [dispatch]);

  const withMiniLoader = useCallback(
    async <T>(asyncFn: () => Promise<T>): Promise<T> => {
      try {
        showMiniLoader();
        const result = await asyncFn();
        return result;
      } finally {
        hideMiniLoader();
      }
    },
    [showMiniLoader, hideMiniLoader]
  );

  return {
    // ê¸°ë³¸ ë¡œë” (ì´ì œ ë¯¸ë‹ˆë¡œë” ì‚¬ìš©) - ê²Œì‹œê¸€ ì‘ì„± ì™¸ ëª¨ë“  ìƒí™©
    showLoader,
    hideLoader,
    withLoader,
    setLoaderText,
    
    // ê²Œì‹œê¸€ ì‘ì„±ìš© í’€ìŠ¤í¬ë¦° ë¡œë”
    showPostLoader,
    hidePostLoader,
    withPostLoader,
    setPostLoaderText,
    
    // ì§ì ‘ ë¯¸ë‹ˆ ë¡œë” ì œì–´
    showMiniLoader,
    hideMiniLoader,
    withMiniLoader,
    setMiniLoaderText,
  };
}; 