import React, { forwardRef } from 'react';

export interface SwitchProps extends Omit<React.InputHTMLAttributes<HTMLInputElement>, 'size' | 'onChange'> {
  /** í™œì„±í™” ìƒíƒœ */
  checked?: boolean;
  /** ê°’ ë³€ê²½ ì‹œ í˜¸ì¶œë˜ëŠ” í•¨ìˆ˜ */
  onChange?: (checked: boolean, event: React.ChangeEvent<HTMLInputElement>) => void;
  /** ë¼ë²¨ í…ìŠ¤íŠ¸ */
  label?: string;
  /** ì—ëŸ¬ ë©”ì‹œì§€ */
  error?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ */
  helper?: string;
  /** í¬ê¸° variant */
  size?: 'sm' | 'md' | 'lg';
  /** ë¼ë²¨ ìœ„ì¹˜ */
  labelPosition?: 'left' | 'right';
  /** í™œì„±í™” ì‹œ í‘œì‹œí•  í…ìŠ¤íŠ¸ */
  onText?: string;
  /** ë¹„í™œì„±í™” ì‹œ í‘œì‹œí•  í…ìŠ¤íŠ¸ */
  offText?: string;
  /** ì»¨í…Œì´ë„ˆ í´ë˜ìŠ¤ ì´ë¦„ */
  containerClassName?: string;
  /** ë¼ë²¨ í´ë˜ìŠ¤ ì´ë¦„ */
  labelClassName?: string;
  /** ì—ëŸ¬ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  errorClassName?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  helperClassName?: string;
}

const Switch = forwardRef<HTMLInputElement, SwitchProps>(({
  checked = false,
  onChange,
  label,
  error,
  helper,
  size = 'md',
  labelPosition = 'right',
  onText,
  offText,
  disabled = false,
  className = '',
  containerClassName = '',
  labelClassName = '',
  errorClassName = '',
  helperClassName = '',
  id,
  ...props
}, ref) => {
  const switchId = id || `switch-${Math.random().toString(36).substr(2, 9)}`;

  // í¬ê¸°ë³„ í´ë˜ìŠ¤ ì •ì˜
  const sizeClasses = {
    sm: {
      track: 'w-8 h-4',
      thumb: 'w-3 h-3',
      translate: 'translate-x-4',
      text: 'text-xs px-1'
    },
    md: {
      track: 'w-10 h-5',
      thumb: 'w-4 h-4',
      translate: 'translate-x-5',
      text: 'text-xs px-1.5'
    },
    lg: {
      track: 'w-12 h-6',
      thumb: 'w-5 h-5',
      translate: 'translate-x-6',
      text: 'text-sm px-2'
    }
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onChange?.(event.target.checked, event);
  };

  const currentSize = sizeClasses[size];

  // track í´ë˜ìŠ¤
  const trackClasses = `
    relative inline-flex items-center
    ${currentSize.track}
    rounded-full
    transition-colors duration-200 ease-in-out
    focus-within:outline-none
    focus-within:ring-2
    focus-within:ring-blue-500/20
    ${checked
      ? error
        ? 'bg-red-500'
        : 'bg-blue-600'
      : error
        ? 'bg-red-200'
        : 'bg-gray-300'
    }
    ${disabled
      ? 'opacity-50 cursor-not-allowed'
      : 'cursor-pointer'
    }
    ${className}
  `.trim().replace(/\s+/g, ' ');

  // thumb í´ë˜ìŠ¤
  const thumbClasses = `
    inline-block
    ${currentSize.thumb}
    bg-white
    rounded-full
    shadow
    transform
    transition-transform duration-200 ease-in-out
    ${checked ? currentSize.translate : 'translate-x-0.5'}
  `.trim().replace(/\s+/g, ' ');

  const switchElement = (
    <div className="relative inline-flex">
      <input
        ref={ref}
        id={switchId}
        type="checkbox"
        checked={checked}
        onChange={handleChange}
        disabled={disabled}
        className="sr-only"
        {...props}
      />
      <label
        htmlFor={switchId}
        className={trackClasses}
      >
        {/* ìƒíƒœ í…ìŠ¤íŠ¸ (ì„ íƒì‚¬í•­) */}
        {(onText || offText) && (
          <span className={`
            absolute inset-0 flex items-center justify-center
            font-sans font-medium text-white
            ${currentSize.text}
            pointer-events-none
          `}>
            {checked ? onText : offText}
          </span>
        )}

        {/* Thumb */}
        <span className={thumbClasses} />
      </label>
    </div>
  );

  const labelElement = label && (
    <label
      htmlFor={switchId}
      className={`
        text-gray-700
        text-xs sm:text-sm
        font-normal
        font-sans
        select-none
        ${disabled ? 'opacity-50 cursor-not-allowed' : 'cursor-pointer'}
        ${labelClassName}
      `.trim().replace(/\s+/g, ' ')}
    >
      {label}
    </label>
  );

  return (
    <div className={containerClassName}>
      {/* Switchì™€ ë¼ë²¨ */}
      <div className={`inline-flex items-center gap-3 ${labelPosition === 'left' ? 'flex-row-reverse' : ''}`}>
        {switchElement}
        {labelElement}
      </div>

      {/* ì—ëŸ¬ ë©”ì‹œì§€ */}
      {error && (
        <p className={`
          mt-2
          text-sm
          text-red-500
          font-sans
          ${errorClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {error}
        </p>
      )}

      {/* ë„ì›€ë§ í…ìŠ¤íŠ¸ */}
      {helper && !error && (
        <p className={`
          mt-2
          text-sm
          text-gray-500
          font-sans
          ${helperClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {helper}
        </p>
      )}
    </div>
  );
});

Switch.displayName = 'Switch';

export { Switch };