import React, { forwardRef } from 'react';

export interface InputProps extends Omit<React.InputHTMLAttributes<HTMLInputElement>, 'size'> {
  /** Input ë¼ë²¨ */
  label?: string;
  /** ì—ëŸ¬ ë©”ì‹œì§€ */
  error?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ */
  helper?: string;
  /** ì…ë ¥ íƒ€ì… */
  type?: 'text' | 'email' | 'password' | 'number' | 'tel' | 'url' | 'search';
  /** í¬ê¸° variant */
  size?: 'sm' | 'md' | 'lg';
  /** ì „ì²´ ë„ˆë¹„ ì‚¬ìš© ì—¬ë¶€ */
  fullWidth?: boolean;
  /** ì…ë ¥ ì•ìª½ ì•„ì´ì½˜ ë˜ëŠ” í…ìŠ¤íŠ¸ */
  startAdornment?: React.ReactNode;
  /** ì…ë ¥ ë’¤ìª½ ì•„ì´ì½˜ ë˜ëŠ” í…ìŠ¤íŠ¸ */
  endAdornment?: React.ReactNode;
  /** ì»¨í…Œì´ë„ˆ í´ë˜ìŠ¤ ì´ë¦„ */
  containerClassName?: string;
  /** ë¼ë²¨ í´ë˜ìŠ¤ ì´ë¦„ */
  labelClassName?: string;
  /** ì—ëŸ¬ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  errorClassName?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  helperClassName?: string;
}

const Input = forwardRef<HTMLInputElement, InputProps>(({
  label,
  error,
  helper,
  type = 'text',
  size = 'md',
  fullWidth = false,
  startAdornment,
  endAdornment,
  className = '',
  containerClassName = '',
  labelClassName = '',
  errorClassName = '',
  helperClassName = '',
  disabled = false,
  required = false,
  id,
  ...props
}, ref) => {
  const inputId = id || `input-${Math.random().toString(36).substr(2, 9)}`;

  // í¬ê¸°ë³„ í´ë˜ìŠ¤ ì •ì˜
  const sizeClasses = {
    sm: 'h-8 px-2 text-sm',
    md: 'h-10 px-3 text-sm',
    lg: 'h-12 px-4 text-base'
  };

  // ê¸°ë³¸ input í´ë˜ìŠ¤
  const baseInputClasses = `
    w-full
    font-sans
    text-gray-900
    bg-white
    border
    rounded-lg
    transition-all
    duration-200
    focus:outline-none
    focus:ring-2
    focus:ring-blue-500/20
    disabled:opacity-50
    disabled:cursor-not-allowed
    disabled:bg-gray-50
  `;

  // ìƒíƒœë³„ í…Œë‘ë¦¬ ìƒ‰ìƒ
  const borderClasses = error
    ? 'border-red-500 focus:border-red-500'
    : 'border-gray-300 focus:border-blue-500 hover:border-gray-400';

  // input í´ë˜ìŠ¤ ì¡°í•©
  const inputClasses = `
    ${baseInputClasses}
    ${sizeClasses[size]}
    ${borderClasses}
    ${startAdornment ? 'pl-8' : ''}
    ${endAdornment ? 'pr-8' : ''}
    ${className}
  `.trim().replace(/\s+/g, ' ');

  return (
    <div className={`${fullWidth ? 'w-full' : 'w-auto'} ${containerClassName}`}>
      {/* ë¼ë²¨ */}
      {label && (
        <label
          htmlFor={inputId}
          className={`
            block
            text-sm
            font-medium
            text-gray-700
            mb-2
            font-sans
            ${required ? "after:content-['*'] after:text-red-500 after:ml-1" : ''}
            ${disabled ? 'opacity-50' : ''}
            ${labelClassName}
          `.trim().replace(/\s+/g, ' ')}
        >
          {label}
        </label>
      )}

      {/* Input ì»¨í…Œì´ë„ˆ */}
      <div className="relative">
        {/* ì‹œì‘ ì•„ì´ì½˜/í…ìŠ¤íŠ¸ */}
        {startAdornment && (
          <div className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500 pointer-events-none">
            {startAdornment}
          </div>
        )}

        {/* Input ìš”ì†Œ */}
        <input
          ref={ref}
          id={inputId}
          type={type}
          disabled={disabled}
          required={required}
          className={inputClasses}
          {...props}
        />

        {/* ë ì•„ì´ì½˜/í…ìŠ¤íŠ¸ */}
        {endAdornment && (
          <div className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-500">
            {endAdornment}
          </div>
        )}
      </div>

      {/* ì—ëŸ¬ ë©”ì‹œì§€ */}
      {error && (
        <p className={`
          mt-2
          text-sm
          text-red-500
          font-sans
          ${errorClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {error}
        </p>
      )}

      {/* ë„ì›€ë§ í…ìŠ¤íŠ¸ */}
      {helper && !error && (
        <p className={`
          mt-2
          text-sm
          text-gray-500
          font-sans
          ${helperClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {helper}
        </p>
      )}
    </div>
  );
});

Input.displayName = 'Input';

export { Input };