/**
 * ê¸°ë³¸ í…Œë§ˆì™€ ì˜¤ë²„ë¼ì´ë“œë¥¼ ë³‘í•©í•˜ì—¬ ìµœì¢… í…Œë§ˆë¥¼ ìƒì„±í•©ë‹ˆë‹¤.
 * 
 * @param baseTheme ê¸°ë³¸ í…Œë§ˆ
 * @param override ì‚¬ìš©ì ì •ì˜ ì˜¤ë²„ë¼ì´ë“œ
 * @returns ë³‘í•©ëœ ìµœì¢… í…Œë§ˆ
 * 
 * @example
 * ```typescript
 * const customTheme = mergeTheme(defaultLightTheme, {
 *   colors: {
 *     semantic: {
 *       primary: {
 *         500: '#ff6b35', // ë©”ì¸ ì»¬ëŸ¬ë§Œ ë³€ê²½
 *       }
 *     }
 *   }
 * });
 * ```
 */
export function mergeTheme(baseTheme: Theme, override: ThemeOverride): Theme {
  return deepMerge(baseTheme, override);
}

