/**
 * ì‚¬ìš©ì ì •ì˜ í…Œë§ˆë¥¼ ì‰½ê²Œ ìƒì„±í•  ìˆ˜ ìˆëŠ” í†µí•© í•¨ìˆ˜
 * 
 * @param config í…Œë§ˆ êµ¬ì„± ì„¤ì •
 * @returns ì™„ì „í•œ í…Œë§ˆ ì˜¤ë²„ë¼ì´ë“œ
 * 
 * @example
 * ```typescript
 * const myTheme = createCustomTheme({
 *   brandColor: '#ff6b35',
 *   fontFamily: ['"Inter"', 'sans-serif'],
 *   borderRadius: 'lg',
 *   components: {
 *     button: { borderRadius: 'full' }
 *   }
 * });
 * ```
 */
export function createCustomTheme(config: {
  // ìƒ‰ìƒ ì„¤ì •
  brandColor?: string;
  colors?: {
    secondary?: string;
    success?: string;
    warning?: string;
    error?: string;
    info?: string;
  };
  
  // íƒ€ì´í¬ê·¸ë˜í”¼ ì„¤ì •
  fontFamily?: string[];
  fontScale?: { baseSize?: number; ratio?: number };
  
  // ê°„ê²© ë° í¬ê¸° ì„¤ì •
  borderRadius?: 'sm' | 'md' | 'lg' | 'xl';
  spacing?: 'tight' | 'normal' | 'relaxed';
  
  // ì»´í¬ë„ŒíŠ¸ ì„¤ì •
  components?: Partial<ComponentTokens>;
}): ThemeOverride {
  const override: ThemeOverride = {};
  
  // ìƒ‰ìƒ ì„¤ì •
  if (config.brandColor || config.colors) {
    override.colors = {
      semantic: config.brandColor 
        ? createSemanticColors(config.brandColor, config.colors)
        : undefined
    };
  }
  
  // íƒ€ì´í¬ê·¸ë˜í”¼ ì„¤ì •
  if (config.fontFamily || config.fontScale) {
    override.typography = createTypographyConfig({
      fontFamily: config.fontFamily ? { sans: config.fontFamily } : undefined,
      fontScale: config.fontScale
    });
  }
  
  // ì»´í¬ë„ŒíŠ¸ ì„¤ì •
  if (config.components) {
    override.components = config.components;
  }
  
  return override;
}

