'use client';

import { useAppDispatch, useAppSelector } from '@hooks/common';
import { showToast } from '@redux/Features/Toast/toastSlice';
import { selectUserInfo } from '@redux/Features/User/userSlice';
import { getUserLevel, hasLevelPermission, isAdmin } from '@util/authUtil';
import { getCurrentUserFromToken } from '@util/jwtUtil';
import { useCallback, useEffect, useState } from 'react';

export const usePermission = () => {
  const dispatch = useAppDispatch();
  const userInfo = useAppSelector(selectUserInfo);
  const reduxUser = userInfo?.userInfo;
  const [jwtUser, setJwtUser] = useState<any>(null);

  // í˜„ì¬ ì‚¬ìš©ì: Redux ì‚¬ìš©ì ì •ë³´ë¥¼ ìš°ì„ í•˜ë˜, ì—†ìœ¼ë©´ JWT ì •ë³´ ì‚¬ìš© (ê·¹íˆ ì˜ˆì™¸ì  ìƒí™©)
  const currentUser = reduxUser || jwtUser;
  const userLevel = getUserLevel(currentUser);
  const adminStatus = isAdmin(currentUser);
  const isLoggedIn = userInfo?.isLoggedIn || !!jwtUser;

  // JWT í† í°ì—ì„œ ì‚¬ìš©ì ì •ë³´ ê°€ì ¸ì˜¤ê¸° (Reduxì— ì •ë³´ê°€ ì—†ì„ ë•Œë§Œ ëŒ€ë¹„ìš©)
  useEffect(() => {
    const getJwtUser = async () => {
      // Reduxì— ì‚¬ìš©ì ì •ë³´ê°€ ì´ë¯¸ ìˆìœ¼ë©´ JWT ì¡°íšŒ ìƒëµ
      if (reduxUser) {
        setJwtUser(null);
        return;
      }

      try {
        const tokenUser = await getCurrentUserFromToken();
        setJwtUser(tokenUser);
      } catch (error) {
        // JWT í† í°ì´ ì—†ê±°ë‚˜ ìœ íš¨í•˜ì§€ ì•Šì€ ê²½ìš°
        setJwtUser(null);
      }
    };

    getJwtUser();
  }, [reduxUser]);

  // ê¶Œí•œ ì²´í¬ í•¨ìˆ˜ - Redux store ì •ë³´ ìš°ì„  ì‚¬ìš© (ë™ê¸° ì²˜ë¦¬)
  const checkPermission = useCallback(
    (requiredLevel?: number) => {
      if (requiredLevel === undefined) return true;

      // Redux storeì˜ ì‚¬ìš©ì ì •ë³´ê°€ ìˆìœ¼ë©´ ìš°ì„  ì‚¬ìš© (ì´ê²ƒì´ ì¼ë°˜ì ì¸ ê²½ìš°)
      if (reduxUser) {
        return hasLevelPermission(reduxUser, requiredLevel);
      }

      // Reduxì— ì •ë³´ê°€ ì—†ëŠ” ì˜ˆì™¸ì  ìƒí™©ì—ì„œë§Œ JWT ì‚¬ìš©
      if (jwtUser) {
        console.warn(
          'Redux storeì— ì‚¬ìš©ì ì •ë³´ê°€ ì—†ì–´ JWT í† í° ì •ë³´ë¥¼ ì‚¬ìš©í•©ë‹ˆë‹¤.'
        );
        return hasLevelPermission(jwtUser, requiredLevel);
      }

      // ì‚¬ìš©ì ì •ë³´ê°€ ì „í˜€ ì—†ëŠ” ê²½ìš°
      return requiredLevel === 0;
    },
    [reduxUser, jwtUser]
  );

  // ê¶Œí•œì´ ì—†ëŠ” ê²½ìš° í† ìŠ¤íŠ¸ ë©”ì‹œì§€ í‘œì‹œ
  const showAccessDeniedToast = useCallback(() => {
    dispatch(
      showToast({
        title: 'ì ‘ê·¼ ê¶Œí•œì´ ì—†ìŠµë‹ˆë‹¤.',
        content: 'í•´ë‹¹ ë©”ë‰´ì— ì ‘ê·¼í•  ê¶Œí•œì´ ì—†ìŠµë‹ˆë‹¤.',
        headerTextColor: 'text-red-500',
      })
    );
  }, [dispatch]);

  return {
    checkPermission,
    showAccessDeniedToast,
    userLevel,
    isAdmin: adminStatus,
    isLoggedIn,
    currentUser,
  };
};
