import React, { forwardRef } from 'react';

export interface CheckboxProps extends Omit<React.InputHTMLAttributes<HTMLInputElement>, 'size' | 'onChange'> {
  /** ì²´í¬ ìƒíƒœ */
  checked?: boolean;
  /** ê°’ ë³€ê²½ ì‹œ í˜¸ì¶œë˜ëŠ” í•¨ìˆ˜ */
  onChange?: (checked: boolean, event: React.ChangeEvent<HTMLInputElement>) => void;
  /** ë¼ë²¨ í…ìŠ¤íŠ¸ */
  label?: string;
  /** ì—ëŸ¬ ë©”ì‹œì§€ */
  error?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ */
  helper?: string;
  /** í¬ê¸° variant */
  size?: 'sm' | 'md' | 'lg';
  /** ì¤‘ê°„ ìƒíƒœ (indeterminate) */
  indeterminate?: boolean;
  /** ë¼ë²¨ ìœ„ì¹˜ */
  labelPosition?: 'left' | 'right';
  /** ì»¨í…Œì´ë„ˆ í´ë˜ìŠ¤ ì´ë¦„ */
  containerClassName?: string;
  /** ë¼ë²¨ í´ë˜ìŠ¤ ì´ë¦„ */
  labelClassName?: string;
  /** ì—ëŸ¬ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  errorClassName?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  helperClassName?: string;
}

const Checkbox = forwardRef<HTMLInputElement, CheckboxProps>(({
  checked = false,
  onChange,
  label,
  error,
  helper,
  size = 'md',
  indeterminate = false,
  labelPosition = 'right',
  disabled = false,
  className = '',
  containerClassName = '',
  labelClassName = '',
  errorClassName = '',
  helperClassName = '',
  id,
  ...props
}, ref) => {
  const checkboxId = id || `checkbox-${Math.random().toString(36).substr(2, 9)}`;

  // í¬ê¸°ë³„ í´ë˜ìŠ¤ ì •ì˜
  const sizeClasses = {
    sm: 'w-3 h-3',
    md: 'w-4 h-4',
    lg: 'w-5 h-5'
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onChange?.(event.target.checked, event);
  };

  // ê¸°ë³¸ checkbox í´ë˜ìŠ¤
  const baseCheckboxClasses = `
    text-blue-600
    bg-white
    border-2
    rounded
    transition-all
    duration-200
    focus:outline-none
    focus:ring-2
    focus:ring-blue-500/20
    focus:border-blue-500
    disabled:opacity-50
    disabled:cursor-not-allowed
    disabled:bg-gray-50
  `;

  // ìƒíƒœë³„ í…Œë‘ë¦¬ ìƒ‰ìƒ
  const borderClasses = error
    ? 'border-red-500'
    : 'border-gray-300 hover:border-blue-500';

  // checkbox í´ë˜ìŠ¤ ì¡°í•©
  const checkboxClasses = `
    ${baseCheckboxClasses}
    ${sizeClasses[size]}
    ${borderClasses}
    ${className}
  `.trim().replace(/\s+/g, ' ');

  const checkboxElement = (
    <input
      ref={ref}
      id={checkboxId}
      type="checkbox"
      checked={checked}
      onChange={handleChange}
      disabled={disabled}
      className={checkboxClasses}
      {...props}
    />
  );

  const labelElement = label && (
    <label
      htmlFor={checkboxId}
      className={`
        text-gray-700
        text-xs sm:text-sm
        font-normal
        font-sans
        select-none
        ${disabled ? 'opacity-50 cursor-not-allowed' : 'cursor-pointer'}
        ${labelClassName}
      `.trim().replace(/\s+/g, ' ')}
    >
      {label}
    </label>
  );

  return (
    <div className={containerClassName}>
      {/* Checkboxì™€ ë¼ë²¨ */}
      <div className={`flex items-center gap-2 ${labelPosition === 'left' ? 'flex-row-reverse' : ''}`}>
        {checkboxElement}
        {labelElement}
      </div>

      {/* ì—ëŸ¬ ë©”ì‹œì§€ */}
      {error && (
        <p className={`
          mt-2
          text-sm
          text-red-500
          font-sans
          ${errorClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {error}
        </p>
      )}

      {/* ë„ì›€ë§ í…ìŠ¤íŠ¸ */}
      {helper && !error && (
        <p className={`
          mt-2
          text-sm
          text-gray-500
          font-sans
          ${helperClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {helper}
        </p>
      )}
    </div>
  );
});

Checkbox.displayName = 'Checkbox';

export { Checkbox };