/**
 * URL íŒŒë¼ë¯¸í„°ë¥¼ ì•ˆì „í•˜ê²Œ ì¶”ê°€/ìˆ˜ì •í•˜ëŠ” ìœ í‹¸ë¦¬í‹° í•¨ìˆ˜
 */
export const addUrlParams = (
  baseUrl: string, 
  params: Record<string, string>
): string => {
  try {
    // ì ˆëŒ€ URLì¸ì§€ ìƒëŒ€ URLì¸ì§€ í™•ì¸
    const url = baseUrl.startsWith('http') 
      ? new URL(baseUrl)
      : new URL(baseUrl, window.location.origin);
    
    const searchParams = new URLSearchParams(url.search);
    
    // ìƒˆë¡œìš´ íŒŒë¼ë¯¸í„°ë“¤ì„ ì¶”ê°€/ì—…ë°ì´íŠ¸
    Object.entries(params).forEach(([key, value]) => {
      searchParams.set(key, value);
    });
    
    // pathnameê³¼ ìƒˆë¡œìš´ search paramsë¥¼ ê²°í•©
    return `${url.pathname}?${searchParams.toString()}`;
  } catch (error) {
    console.error('URL íŒŒì‹± ì˜¤ë¥˜:', error);
    throw new Error('ìœ íš¨í•˜ì§€ ì•Šì€ URLì…ë‹ˆë‹¤.');
  }
};

/**
 * sessionStorageì—ì„œ ì´ì „ í˜ì´ì§€ URLì„ ê°€ì ¸ì™€ì„œ íŒŒë¼ë¯¸í„°ë¥¼ ì¶”ê°€í•œ í›„ ë°˜í™˜
 */
export const getPreviousUrlWithParams = (
  storageKey: string,
  params: Record<string, string>,
  fallbackUrl: string = '/'
): string => {
  const previousUrl = sessionStorage.getItem(storageKey);
  
  if (!previousUrl) {
    return fallbackUrl;
  }
  
  try {
    return addUrlParams(previousUrl, params);
  } catch (error) {
    console.error('ì´ì „ URL ì²˜ë¦¬ ì˜¤ë¥˜:', error);
    return fallbackUrl;
  }
}; 