/**
 * ìˆ«ìë¥¼ 1000 ë‹¨ìœ„ë¡œ ì½¤ë§ˆ(,)ë¥¼ ì°ì–´ì„œ ë¬¸ìì—´ë¡œ ë³€í™˜
 * @param num ë³€í™˜í•  ìˆ«ì ë˜ëŠ” ìˆ«ì í˜•íƒœì˜ ë¬¸ìì—´
 * @returns 1000ë‹¨ìœ„ë¡œ ì½¤ë§ˆê°€ í¬í•¨ëœ ë¬¸ìì—´
 */
export const formatNumberWithComma = (num: number | string): string => {

    if (num === null || num === undefined || num === '') {
        return '';
    }
    
    // ìˆ«ìë‚˜ ë¬¸ìì—´ì„ ë¬¸ìì—´ë¡œ ë³€í™˜
    const numStr: string = typeof num === 'number' ? num.toString() : num;
    
    // ì†Œìˆ˜ì ì´ ìˆëŠ” ê²½ìš° ì²˜ë¦¬
    const parts = numStr.split('.');
    
    // ì •ìˆ˜ ë¶€ë¶„ì— 3ìë¦¬ë§ˆë‹¤ ì½¤ë§ˆ ì¶”ê°€
    parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    
    // ì†Œìˆ˜ì ì´ ìˆìœ¼ë©´ ë‹¤ì‹œ í•©ì¹˜ê³ , ì—†ìœ¼ë©´ ì •ìˆ˜ ë¶€ë¶„ë§Œ ë°˜í™˜
    return parts.length > 1 ? parts.join('.') : parts[0];
};

/**
 * ë¬¸ìì—´ì—ì„œ ì½¤ë§ˆë¥¼ ì œê±°í•˜ê³  ìˆ«ìë¡œ ë³€í™˜
 * @param str ì½¤ë§ˆê°€ í¬í•¨ëœ ë¬¸ìì—´
 * @returns ì½¤ë§ˆê°€ ì œê±°ëœ ìˆ«ì
 */
export const removeCommaFromString = (str: string): number => {
  if (!str) return 0;
  return parseFloat(str.replace(/,/g, ''));
};
