import type { CommonResponse } from "../../types/common";
import commentService from "@services/commentService";
import { useQuery } from "@tanstack/react-query";
import { timeAgo } from "@util/dateUtil";

// ëŒ“ê¸€ ì¡°íšŒë¥¼ ìœ„í•œ ì¸í„°í˜ì´ìŠ¤
interface CommentQueryParams {
  postId: number;
  page: number;
  pageSize?: number;
}

// ëŒ“ê¸€ ì¡°íšŒ í›…
export const useCommentQuery = (
  params: CommentQueryParams,
  options?: { enabled?: boolean }
) => {

  
  return useQuery({
    queryKey: ["comments", params],
    queryFn: () => {
      return commentService.getComments(params);
    },
    staleTime: 1000 * 60 * 5, // 5ë¶„ ë™ì•ˆ ìºì‹œ ìœ ì§€
    gcTime: 1000 * 60 * 30, // 30ë¶„ ë™ì•ˆ ìºì‹œ ì €ì¥
    retry: 2, // ì‹¤íŒ¨ ì‹œ 2ë²ˆê¹Œì§€ ì¬ì‹œë„
    enabled: options?.enabled ?? true,
    select: (data: CommonResponse<any>) => {
      
      if (data.successOrNot === "N") {
        return {
          data: {
            items: [],
            totalCount: 0,
          },
        };
      }
      if (!data.data) {
        return {
          data: {
            items: [],
            totalCount: 0,
          },
        };
      }
      
      const result = {
        data: {
          items: Array.isArray(data.data.items)
            ? data.data.items.map((item: any) => ({
                ...item,
                created_at: timeAgo(item.created_at),
              }))
            : [],
          totalCount: data.data.totalCount || 0,
        },
      };
      
      return result;
    },
  });
};
