import React, { forwardRef } from 'react';

export interface RadioProps extends Omit<React.InputHTMLAttributes<HTMLInputElement>, 'size' | 'onChange'> {
  /** ì²´í¬ ìƒíƒœ */
  checked?: boolean;
  /** ê°’ ë³€ê²½ ì‹œ í˜¸ì¶œë˜ëŠ” í•¨ìˆ˜ */
  onChange?: (value: string, event: React.ChangeEvent<HTMLInputElement>) => void;
  /** Radio ê·¸ë£¹ëª… */
  name: string;
  /** Radio ê°’ */
  value: string;
  /** ë¼ë²¨ í…ìŠ¤íŠ¸ */
  label?: string;
  /** ì—ëŸ¬ ë©”ì‹œì§€ */
  error?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ */
  helper?: string;
  /** í¬ê¸° variant */
  size?: 'sm' | 'md' | 'lg';
  /** ë¼ë²¨ ìœ„ì¹˜ */
  labelPosition?: 'left' | 'right';
  /** ì»¨í…Œì´ë„ˆ í´ë˜ìŠ¤ ì´ë¦„ */
  containerClassName?: string;
  /** ë¼ë²¨ í´ë˜ìŠ¤ ì´ë¦„ */
  labelClassName?: string;
  /** ì—ëŸ¬ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  errorClassName?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  helperClassName?: string;
  /** ìì‹ ìš”ì†Œ (ë¼ë²¨ ëŒ€ì‹  ì‚¬ìš©) */
  children?: React.ReactNode;
}

const Radio = forwardRef<HTMLInputElement, RadioProps>(({
  checked = false,
  onChange,
  name,
  value,
  label,
  error,
  helper,
  size = 'md',
  labelPosition = 'right',
  disabled = false,
  className = '',
  containerClassName = '',
  labelClassName = '',
  errorClassName = '',
  helperClassName = '',
  children,
  id,
  ...props
}, ref) => {
  const radioId = id || `radio-${Math.random().toString(36).substr(2, 9)}`;

  // í¬ê¸°ë³„ í´ë˜ìŠ¤ ì •ì˜
  const sizeClasses = {
    sm: 'w-3 h-3',
    md: 'w-4 h-4',
    lg: 'w-5 h-5'
  };

  const dotSizeClasses = {
    sm: 'w-1.5 h-1.5',
    md: 'w-2.5 h-2.5',
    lg: 'w-3 h-3'
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onChange?.(event.target.value, event);
  };

  // ë¼ë²¨ í…ìŠ¤íŠ¸ ê²°ì •
  const labelText = children || label;

  const radioElement = (
    <div className="relative inline-flex items-center">
      <input
        ref={ref}
        id={radioId}
        type="radio"
        name={name}
        value={value}
        checked={checked}
        onChange={handleChange}
        disabled={disabled}
        className="sr-only"
        {...props}
      />
      <span
        className={`
          flex-shrink-0 rounded-full flex items-center justify-center
          border-2 transition-all duration-200
          ${sizeClasses[size]}
          ${checked
            ? error
              ? 'border-red-500 bg-red-500'
              : 'border-blue-600 bg-blue-600'
            : error
              ? 'border-red-500'
              : 'border-gray-300 hover:border-blue-600'
          }
          ${disabled
            ? 'opacity-50 cursor-not-allowed'
            : 'cursor-pointer'
          }
          ${className}
        `.trim().replace(/\s+/g, ' ')}
      >
        <span
          className={`
            rounded-full transition-all duration-200 bg-white
            ${dotSizeClasses[size]}
            ${checked ? 'opacity-100 scale-100' : 'opacity-0 scale-50'}
          `.trim().replace(/\s+/g, ' ')}
        />
      </span>
    </div>
  );

  const labelElement = labelText && (
    <label
      htmlFor={radioId}
      className={`
        text-gray-700
        text-xs sm:text-sm
        font-normal
        font-sans
        whitespace-nowrap
        select-none
        ${disabled ? 'opacity-50 cursor-not-allowed' : 'cursor-pointer'}
        ${labelClassName}
      `.trim().replace(/\s+/g, ' ')}
    >
      {labelText}
    </label>
  );

  return (
    <div className={containerClassName}>
      {/* Radioì™€ ë¼ë²¨ */}
      <div className={`inline-flex items-center gap-2 ${labelPosition === 'left' ? 'flex-row-reverse' : ''}`}>
        {radioElement}
        {labelElement}
      </div>

      {/* ì—ëŸ¬ ë©”ì‹œì§€ */}
      {error && (
        <p className={`
          mt-2
          text-sm
          text-red-500
          font-sans
          ${errorClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {error}
        </p>
      )}

      {/* ë„ì›€ë§ í…ìŠ¤íŠ¸ */}
      {helper && !error && (
        <p className={`
          mt-2
          text-sm
          text-gray-500
          font-sans
          ${helperClassName}
        `.trim().replace(/\s+/g, ' ')}>
          {helper}
        </p>
      )}
    </div>
  );
});

Radio.displayName = 'Radio';

export { Radio };