import React, { forwardRef, useState, useCallback } from 'react';

export interface TextAreaProps extends React.TextareaHTMLAttributes<HTMLTextAreaElement> {
  /** TextArea ë¼ë²¨ */
  label?: string;
  /** ì—ëŸ¬ ë©”ì‹œì§€ */
  error?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ */
  helper?: string;
  /** í¬ê¸° variant */
  size?: 'sm' | 'md' | 'lg';
  /** ì „ì²´ ë„ˆë¹„ ì‚¬ìš© ì—¬ë¶€ */
  fullWidth?: boolean;
  /** ìë™ ë¦¬ì‚¬ì´ì§• í™œì„±í™” */
  autoResize?: boolean;
  /** ìµœì†Œ ë†’ì´ */
  minHeight?: number;
  /** ìµœëŒ€ ë†’ì´ */
  maxHeight?: number;
  /** ê¸€ì ìˆ˜ ì œí•œ */
  maxLength?: number;
  /** ê¸€ì ìˆ˜ í‘œì‹œ ì—¬ë¶€ */
  showCount?: boolean;
  /** ì»¨í…Œì´ë„ˆ í´ë˜ìŠ¤ ì´ë¦„ */
  containerClassName?: string;
  /** ë¼ë²¨ í´ë˜ìŠ¤ ì´ë¦„ */
  labelClassName?: string;
  /** ì—ëŸ¬ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  errorClassName?: string;
  /** ë„ì›€ë§ í…ìŠ¤íŠ¸ í´ë˜ìŠ¤ ì´ë¦„ */
  helperClassName?: string;
  /** ê¸€ì ìˆ˜ ì¹´ìš´í„° í´ë˜ìŠ¤ ì´ë¦„ */
  countClassName?: string;
}

const TextArea = forwardRef<HTMLTextAreaElement, TextAreaProps>(({
  label,
  error,
  helper,
  size = 'md',
  fullWidth = false,
  autoResize = false,
  minHeight = 80,
  maxHeight = 300,
  maxLength,
  showCount = false,
  disabled = false,
  required = false,
  className = '',
  containerClassName = '',
  labelClassName = '',
  errorClassName = '',
  helperClassName = '',
  countClassName = '',
  value,
  onChange,
  id,
  ...props
}, ref) => {
  const textareaId = id || `textarea-${Math.random().toString(36).substr(2, 9)}`;
  const [textareaHeight, setTextareaHeight] = useState<number>(minHeight);

  // í¬ê¸°ë³„ í´ë˜ìŠ¤ ì •ì˜
  const sizeClasses = {
    sm: 'px-2 py-1.5 text-sm',
    md: 'px-3 py-2 text-sm',
    lg: 'px-4 py-3 text-base'
  };

  // ìë™ ë¦¬ì‚¬ì´ì§• í•¸ë“¤ëŸ¬
  const handleAutoResize = useCallback((element: HTMLTextAreaElement) => {
    if (!autoResize) return;

    // ë†’ì´ ì´ˆê¸°í™”
    element.style.height = 'auto';

    // ìŠ¤í¬ë¡¤ ë†’ì´ ê³„ì‚°
    const scrollHeight = element.scrollHeight;
    const newHeight = Math.min(Math.max(scrollHeight, minHeight), maxHeight);

    setTextareaHeight(newHeight);
    element.style.height = `${newHeight}px`;
  }, [autoResize, minHeight, maxHeight]);

  // ê°’ ë³€ê²½ í•¸ë“¤ëŸ¬
  const handleChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    // ìµœëŒ€ ê¸¸ì´ ì²´í¬
    if (maxLength && event.target.value.length > maxLength) {
      return;
    }

    onChange?.(event);

    // ìë™ ë¦¬ì‚¬ì´ì§•
    if (autoResize) {
      handleAutoResize(event.target);
    }
  };

  // ê¸°ë³¸ textarea í´ë˜ìŠ¤
  const baseTextareaClasses = `
    w-full
    font-sans
    text-gray-900
    bg-white
    border
    rounded-lg
    transition-all
    duration-200
    focus:outline-none
    focus:ring-2
    focus:ring-blue-500/20
    disabled:opacity-50
    disabled:cursor-not-allowed
    disabled:bg-gray-50
    resize-none
  `;

  // ìƒíƒœë³„ í…Œë‘ë¦¬ ìƒ‰ìƒ
  const borderClasses = error
    ? 'border-red-500 focus:border-red-500'
    : 'border-gray-300 focus:border-blue-500 hover:border-gray-400';

  // textarea í´ë˜ìŠ¤ ì¡°í•©
  const textareaClasses = `
    ${baseTextareaClasses}
    ${sizeClasses[size]}
    ${borderClasses}
    ${autoResize ? 'resize-none' : 'resize-y'}
    ${className}
  `.trim().replace(/\s+/g, ' ');

  // í˜„ì¬ ê¸€ì ìˆ˜ ê³„ì‚°
  const currentLength = typeof value === 'string' ? value.length : 0;
  const isOverLimit = maxLength ? currentLength > maxLength : false;

  return (
    <div className={`${fullWidth ? 'w-full' : 'w-auto'} ${containerClassName}`}>
      {/* ë¼ë²¨ ë° ê¸€ì ìˆ˜ í‘œì‹œ */}
      <div className="flex justify-between items-center mb-2">
        {/* ë¼ë²¨ */}
        {label && (
          <label
            htmlFor={textareaId}
            className={`
              block
              text-sm
              font-medium
              text-gray-700
              font-sans
              ${required ? "after:content-['*'] after:text-red-500 after:ml-1" : ''}
              ${disabled ? 'opacity-50' : ''}
              ${labelClassName}
            `.trim().replace(/\s+/g, ' ')}
          >
            {label}
          </label>
        )}

        {/* ê¸€ì ìˆ˜ í‘œì‹œ */}
        {showCount && (maxLength || currentLength > 0) && (
          <span className={`
            text-sm
            font-sans
            ${isOverLimit
              ? 'text-red-500'
              : currentLength / (maxLength || 100) > 0.8
                ? 'text-orange-500'
                : 'text-gray-500'
            }
            ${countClassName}
          `.trim().replace(/\s+/g, ' ')}>
            {currentLength}{maxLength && `/${maxLength}`}
          </span>
        )}
      </div>

      {/* TextArea ìš”ì†Œ */}
      <textarea
        ref={ref}
        id={textareaId}
        value={value}
        onChange={handleChange}
        disabled={disabled}
        required={required}
        maxLength={maxLength}
        className={textareaClasses}
        style={autoResize ? { height: textareaHeight, minHeight, maxHeight } : { minHeight }}
        {...props}
      />

      {/* í•˜ë‹¨ ë©”ì‹œì§€ ì˜ì—­ */}
      <div className="flex justify-between items-start mt-2">
        {/* ì—ëŸ¬ ë©”ì‹œì§€ ë˜ëŠ” ë„ì›€ë§ í…ìŠ¤íŠ¸ */}
        <div className="flex-1">
          {error && (
            <p className={`
              text-sm
              text-red-500
              font-sans
              ${errorClassName}
            `.trim().replace(/\s+/g, ' ')}>
              {error}
            </p>
          )}

          {helper && !error && (
            <p className={`
              text-sm
              text-gray-500
              font-sans
              ${helperClassName}
            `.trim().replace(/\s+/g, ' ')}>
              {helper}
            </p>
          )}
        </div>

        {/* í•˜ë‹¨ ê¸€ì ìˆ˜ í‘œì‹œ (ìƒë‹¨ì— ì—†ì„ ë•Œë§Œ) */}
        {showCount && !label && (maxLength || currentLength > 0) && (
          <span className={`
            text-sm
            font-sans
            ml-2
            flex-shrink-0
            ${isOverLimit
              ? 'text-red-500'
              : currentLength / (maxLength || 100) > 0.8
                ? 'text-orange-500'
                : 'text-gray-500'
            }
            ${countClassName}
          `.trim().replace(/\s+/g, ' ')}>
            {currentLength}{maxLength && `/${maxLength}`}
          </span>
        )}
      </div>
    </div>
  );
});

TextArea.displayName = 'TextArea';

export { TextArea };